@import '../../styles/breakpoints';

.sb-navbar-link {
  align-items: center;
  display: flex;

  @include window-width-max-small {
    align-items: flex-start;
    flex-direction: column;
  }
}

%resting-link-style {
  align-items: center;
  border-bottom: 3px solid transparent;
  border-left: none;
  color: var(--sb-navbar-link-color, var(--sbt-white));
  display: flex;
  gap: 10px;
  text-decoration: none;

  @include window-width-max-small {
    border-bottom: none;
    border-left: 3px solid transparent;
    padding-left: var(--sbt-spacing-40);
  }

  &:hover {
    color: var(
      --sb-navbar-link-color-hover,
      var(--sbt-theme-tertiary-color-20)
    );
  }
}

.sb-navbar-link__active-link {
  @extend %resting-link-style;

  border-color: var(--sb-navbar-link-active-border, var(--sbt-white));
  height: var(--sbt-navbar-height);

  @include window-width-max-small {
    height: auto;
    padding-top: inherit;
  }
}

.sb-navbar-link__non-active-link {
  @extend %resting-link-style;
}
